import React, { lazy, useContext, useEffect, useRef, useState } from 'react'
import { SearchContext } from '../store/SearchContext'
import SearchedArticles from './Search/SearchedArticles'
import axios from 'axios'
import { transformTextToWords, transformedParameters } from '../utils'
import { BrowserContext } from '../store/BrowserContext'
import { TranslationContext } from '../store/TranslationContext'

const Filteration = lazy(()=>import('./Search/Filteration'))
const SearchUsers = lazy(()=>import('./Search/SearchUsers/SearchUsers'))

const Search = () => {
    const [articles, setArticles] = useState([])
    const [loadingSearchedArticles, setLoadingSearchedArticles] = useState(true)
    const [loadingMoreArticles, setLoadingMoreArticles] = useState(false)
    const isNext = useRef(false)
    const url = new URL(window.location)
    const searchParameters = url.searchParams

    
    const APISearchParameters = useRef({})
    const {togglePopup} = useContext(BrowserContext)
    const loadArticles=(APISearchParameters, seen_articles=articles.map(article=> article.id))=>{
        axios.post('/api/article/search-articles/',
            {   search_text_words_list :  JSON.stringify(transformTextToWords(searchParameters.get('searchtext'))) ,
                search_text : searchParameters.get('searchtext'),
                filter_parameters : JSON.stringify(APISearchParameters),
                seen_articles : JSON.stringify(seen_articles)
            },
            {
                headers : {
                  "Content-Type": "multipart/form-data",
                }
        }).then((response)=>{
            setArticles(prev=>[...prev, ...response.data[0]])
            isNext.current = response.data[1]
            setLoadingSearchedArticles(false)
            setLoadingMoreArticles(false)
            togglePopup(false)
        }).catch((error)=>{
            setLoadingSearchedArticles(false)
            setLoadingMoreArticles(false)

        })
    }
    const defaultSearchCategory = url.searchParams.get('searchcategory')
  
    ////////////
    //
    const {setTitle, setDescription} = useContext(BrowserContext)
    const {translate} = useContext(TranslationContext)
    useEffect(()=>{
        setTitle(searchParameters.get('searchtext') + ` | ${translate('Ch7al Machya')}`)
        setDescription(translate('description'))
    },[url])

    const defaultSearchContextValue ={
        articles,
        setArticles,
        loadingSearchedArticles,
        setLoadingSearchedArticles,
        loadArticles,
        isNext : isNext.current,
        loadingMoreArticles,
        setLoadingMoreArticles,
        APISearchParameters,

    }
    console.log(defaultSearchCategory)
    return (
        <SearchContext.Provider value={defaultSearchContextValue} >
            { defaultSearchCategory === 'vehicles' && <><Filteration/><SearchedArticles/></>}
            { (defaultSearchCategory === 'users' || defaultSearchCategory === 'pages') && <SearchUsers/> } 
        </SearchContext.Provider>
    )
}

export default Search