import React, { useContext, useEffect, useRef, useState } from 'react'
import ArticleCard1 from '../../Components/ArticleCard1'
import ArticleCard1Loading from '../../Components/ArticleCard1Loading'
import { SearchContext } from '../../store/SearchContext'
import { TranslationContext } from '../../store/TranslationContext'

const SearchedArticles = () => {
    const {loadingSearchedArticles, articles, loadArticles, isNext, loadingMoreArticles, setLoadingMoreArticles, APISearchParameters} = useContext(SearchContext)
    let count = 0
    const [selectedArticle, setSelectedArticle] = useState(null)
    const {translate} =useContext(TranslationContext)
    return (
        
        <div className='pb-5'>
            <div className="col-12 d-flex flex-row flex-wrap">
                { loadingSearchedArticles ?
                    <>
                        {Array.from({length: 20}, (_, x) => x).map((i)=>{
                            return(
                                <ArticleCard1Loading key={i} />
                            )
                        })}
                    </>:
                    articles.length === 0 ? 
                        <div className='my-5 mx-auto'><h3 className='my-4 text-center' >{translate('No article was found.')}</h3></div>:
                        articles.map((article)=>{
                            count +=1
                            return(
                                <React.Fragment key={article.id}>
                                    <ArticleCard1 selectedArticle={selectedArticle} setSelectedArticle={setSelectedArticle} article={article}/>
                                </React.Fragment>
                            )
                        })
                }
            </div>
            {isNext && 
            <div className='px-1'>
                <div className='py-3 d-flex justify-content-center rounded-top-0'>
                    <button disabled={loadingMoreArticles} onClick={()=>{setLoadingMoreArticles(true); loadArticles(APISearchParameters.current)}} className={`rounded-circle border border-3 bg-color-white border-dark w-fit-content aspect-ratio-1 p-3`}><i className="fa-solid fa-plus fa-2xl"></i></button>
                </div>
            </div>
                
            }   
        </div>
        
    )
}

export default SearchedArticles